* {
  margin: 0;
  padding: 0;
}

body {
  margin: 50px;
  background-color: white;
  background-size: cover;
}

header {
  margin-bottom: 20px;
}

h1, h2, h3 {
  font-family: Helvetica, sans-serif;
  letter-spacing: -0.5pt;
}

h1, h2 {
  font-size: 14pt;
}

h3 {
  font-size: 32pt;
}

h2 {
  margin-bottom: 20px;
}

/* (A) FIXED WRAPPER */
.hwrap {
  overflow: hidden; /* HIDE SCROLL BAR */
  background-color: blue;
  margin: 40px 0px;
}
 
/* (B) MOVING TICKER WRAPPER */
.hmove { display: flex; }

/* (C) ITEMS - INTO A LONG HORIZONTAL ROW */
.hitem {
  font-family: "Inconsolata", monospace;
  letter-spacing: -1pt;
  color: white;
  flex-shrink: 0;
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
  text-align: center;
}
 
/* (D) ANIMATION - MOVE ITEMS FROM RIGHT TO LEFT */
/* 4 ITEMS -400%, CHANGE THIS IF YOU ADD/REMOVE ITEMS */
@keyframes tickerh {
  0% { transform: translate3d(100%, 0, 0); }
  100% { transform: translate3d(-600%, 0, 0); }
}
.hmove { animation: tickerh linear 45s infinite; }
.hmove:hover { animation-play-state: paused; }

#alert {
  font-size: 32pt;
}

p, li, a {
  font-family: "Inconsolata", monospace;
  font-size: 14pt;
  letter-spacing: -0.6pt;
  text-transform: lowercase;
}

ul {
  max-width: 900px;
}

li {
  list-style: none;
  line-height: 1em;
  padding: 10px 0px 10px 0px;
  border-bottom: 1px solid black;
}

a {
  text-decoration: none;
  color: blue;
}

a:hover {
  color:darkslategrey;
}

footer {
  margin: 10px 0px;
  color: blue;
}

footer p {
  font-size: 10pt;
}

@media (max-width: 400px) {
  body {
    margin: 40px;
  }

  p, li, a {
    font-size: 12pt;
  }

  header p {
    font-size: 12pt;
  }

  h1, h2 {
    font-size: 12pt;
  }

  h3 {
    font-size: 20pt;
    line-height: 1em;
  }

  footer p {
    font-size: 12pt;
  }

  .hitem {
    font-size: 10pt;
  }
}